@import '../../../../node_modules/slick-carousel/slick/slick.scss';
// @import '../../../../node_modules/slick-carousel/slick/slick-theme.scss';

.slick-slide img {
  margin: 0 auto;
}

.slick-list {
  .slick-loading & {
    background: #fff slick-image-url("img/ajax-loader.gif") center center no-repeat;
  }
}

/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 20px;
    width: 20px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background: transparent;
    color: transparent;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    outline: none;
    &:hover, &:focus {
        outline: none;
        background: transparent;
        color: transparent;
        &:before {
            opacity: 1;
        }
    }
    &.slick-disabled:before {
        opacity: .25;
    }
    &:before {
        line-height: 1;
        color: #fff;
        opacity: .75;
        // -webkit-font-smoothing: antialiased;
        // -moz-osx-font-smoothing: grayscale;
    }
}

.slick-prev {
    left: -25px;
    [dir="rtl"] & {
        left: auto;
        right: -25px;
    }
    &:before {
        content: "prev";
        [dir="rtl"] & {
            content: "prev";
        }
    }
}

.slick-next {
    right: -25px;
    [dir="rtl"] & {
        left: -25px;
        right: auto;
    }
    &:before {
        content: "next";
        [dir="rtl"] & {
            content: "next";
        }
    }
}

/* Dots */

.slick-dotted.slick-slider {
    margin-bottom: 100px;
}

.slick-dots {
    position: absolute;
    bottom: -50px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            // background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            // color: transparent;
            padding: 5px;
            cursor: pointer;

            border-radius: 50%;
            border: 2px solid $primary_color;
            background-color: #fff;
            transition: all .3s;


            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: 1;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: "";
                width: 20px;
                height: 20px;
                line-height: 20px;
                text-align: center;
                color: #fff;
                border-radius: 50%;
//                 opacity: .25;
//                 -webkit-font-smoothing: antialiased;
//                 -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button {
            background-color: #000;
            border-color: #000;
//             opacity: .75;
        }
    }
}



// custom styles
.slider { 
    position: relative;

    &__arrows {}

    &__arrow {
        position: absolute;
        z-index: 1;
        cursor: pointer;
        top: 50%;
        transform: translate(0, -50%);

        &--prev {
            left: 0;
        }
        &--next {
            right: 0;
        }
    }

    &__arrow-icon {
        width: 27px;
        height: 85px;
        stroke: #000;
        stroke-width: 2;
        transition: all .3s;

        &:hover {
            // opacity: .8;
            stroke: $primary_color;
        }
    }

    &__list {
        margin: 0 5rem;
    }

    &--work {
        .slider-item__img {
            width: 17rem;
            height: 17rem;
            margin: 0 auto;
        }
    }
}



.home-top-slider {
  &__item {
    height: 60rem;
  }
}

